import Vue from "vue";
import template from "../assets/template/data.json";

export const getTemplate = ({ dispatch }) => {
  dispatch("GET_TEMPLATE", template);
};

export const getNewsletters = ({ dispatch }) => {
  Vue.http.get("newsletters").then(function (response) {
    dispatch("NEWSLETTERS_RECIEVED", response.data);
  });
};

export const createNewsletter = ({ dispatch }, name) => {
  var content = template;
  Vue.http.post("newsletters", { name: name, content: content }).then(function (response) {
    getNewsletters({dispatch});
  });
};

export const deleteNewsletter = ({ dispatch }, id) => {
  Vue.http.delete("newsletters/" + id).then(function (response) {
    getNewsletters({dispatch});
  });
};

export const getNewsletter = ({ dispatch }, id) => {
  Vue.http.get("newsletters/" + id).then(function (response) {
    dispatch("NEWSLETTER_RECIEVED", response.data);
  });
};

export const saveNewsletter = ({ dispatch }, newsletter) => {
  newsletter.version = (newsletter.version === undefined) ? 0 : newsletter.version + 1;

  Vue.http.put("newsletters/" + newsletter.id, { content: newsletter }).then(function (response) {
    dispatch("NEWSLETTER_SAVED", newsletter.version);
  });
};

export const exportNewsletter = ({ dispatch }, newsletter) => {
  window.open(Vue.http.options.root + "/newsletters/" + newsletter.id + "/export", "_blank");
};

export const deleteBlockItem = ({ dispatch }, index) => {
  dispatch("DELETE_BLOCK_ITEM", index);
};

export const moveSection = ({ dispatch }, index, shift) => {
  dispatch("MOVE_SECTION", Number(index), Number(shift));
};

export const addSection = ({ dispatch }, section) => {
  var d = new Date();
  section.id = d.getTime();
  dispatch("ADD_SECTION", section);
};

export const addArticle = ({ dispatch }, index) => {
  dispatch("ADD_ARTICLE", index);
};



