<template>
  <div class="person">
    <enabler v-bind:enabled.sync="value.enabled" :label="label"></enabler>
    <div class="person__fields">
      <text-field v-bind:value.sync="value.name"></text-field>
      <text-field v-bind:value.sync="value.subtitle"></text-field>
      <picture v-bind:value.sync="value.image"></picture>
    </div>
  </div>
</template>

<script>
import TextField from "./TextField";
import Picture from "./Picture";
import Enabler from "./Enabler";

export default {
  props: ["value", "label"],
  components: {
    TextField, Picture, Enabler
  }
};
</script>


<style lang="scss">
  @import "../assets/scss/ui";

  .person{    
    &__fields{
      >* {margin-bottom: 5px};
    }

    &__label{
      @extend .label;
    }
  }
</style>



