<template>
  <div class="sections-add">
    <select name="" id="" class="input sections-add__input" v-if="opened" v-model="index" transition>
      <option value="-1">Select section</option>
      <option :value="$index" v-for="block in template.blocks">{{block.type}}</option>
    </select>
    
    <button class="sections-add__button" v-on:click="create" v-bind:class="{'sections-add__button--opened' : opened}">Add</button>
  </div>
</template>

<script>
import { addSection } from "../../vuex/actions";

export default {
  vuex: {
    getters: {
      template: state => state.template
    },
    actions: { addSection }
  },

  data () {
    return {
      opened: false,
      index: -1
    };
  },

  methods: {
    create () {
      if (this.opened && this.index >= 0) {
        this.addSection(this.template.blocks[this.index]);
        this.opened = false;
      } else {
        this.name = "";
        this.opened = true;
      }
    }
  }
};
</script>

<style lang="scss">
  @import "../../assets/scss/button.scss";

  .sections-add{
    padding: 30px;
    box-sizing: border-box;
    position: relative;
    width: 100%;
    display: flex;

    &__button{
      @extend .button;
      &--opened{
        border-radius: 0 5px 5px 0;
        width: 30%;
      }    
    }

    &__input{
      width: 75%;
      border-radius: 5px 0 0 5px;
    }

    >*{
      transition: width .25s;

      &.v-enter, &.v-leave{
        width: 0;
      }
    }
  }
  
</style>



