<template>
  <section class="article">
    <text-field label="Section title" v-bind:value.sync="content.title"></text-field>
    <accordion v-bind:title.sync="article.title" v-for="article in content.articles" track-by="$index">      
      <text-field label="Article title" v-bind:value.sync="article.title"></text-field>
      <text-field label="Article content" v-bind:value.sync="article.content" :multiline="true"></text-field>
      <picture v-bind:value.sync="article.image" label="Article illustration"></picture>
      <person label="Writer" v-bind:value.sync="article.person"></person>
      <text-field v-bind:value.sync="article.url" :inline="true"></text-field>
      <enabler v-bind:enabled.sync="article.big" label="Big image layout"></enabler>
    </accordion>
    <button class="button" v-on:click="$dispatch('add')">Add Article</button>
  </section>
</template>

<script>
import TextField from "../../ui/TextField";
import Picture from "../../ui/Picture";
import Person from "../../ui/Person";
import Accordion from "../../ui/Accordion";
import Enabler from "../../ui/Enabler";

export default {
  props: ["content"],
  components: {
    TextField, Picture, Person, Accordion, Enabler
  }
};
</script>


