<template>
  <div class="accordion" v-bind:class="{'accordion--opened':opened}" :draggable="!opened">
    <delete-confirmation v-if="deleteState" v-on:cancel="toggleDeleteState" ></delete-confirmation>
    
    <h2 class="accordion__title" v-on:click="toggle" >{{title}}</h2>
    <img class="accordion__delete" v-if="!deleteState" v-on:click="toggleDeleteState" src="../assets/icons/trash-white.svg" width="15">

    <div class="accordion__content" v-if="opened">
      <slot></slot>
    </div>
  </div>
</template>

<script>
import DeleteConfirmation from "./DeleteConfirmation";

export default {
  props: ["title"],
  data () {
    return {
      opened: false,
      deleteState: false
    };
  },

  components: { DeleteConfirmation },

  methods: {
    toggle () {
      this.opened = !this.opened;
    },
    toggleDeleteState () {
      this.deleteState = !this.deleteState;
    }
  }
};
</script>


<style lang="scss">
  @import "../assets/scss/ui";

  .accordion{
    position: relative;

    &__title{
      background: #287bbb;
      color: #fff;
      cursor: pointer;
      font-size: 13px;
      font-weight: 600;
      padding: 27px 20px 23px 25px;
      text-transform: uppercase;
      margin: 0 0 2px;
      
      
      &:hover{
        background: #287bbb + 10;

        &~img{
          opacity: .5;
        }
      }

      &:before{
        content: "+ ";
        display: inline-block;
        width: 12px;
      }
    }

    &__content{
      padding: 20px 30px 30px;
    }

    &__delete{
      position: absolute;
      right: 25px;
      top: 23px;
      opacity: 0;
      cursor: pointer;
      transition: opacity 0.5s;
      &:hover{
        opacity: 1;
      }
    }
  }

  .accordion--opened > .accordion__title:before{
    content: "- ";
  }

  .accordion .accordion .accordion__content{
    padding: 20px 0 30px;
  }

  .accordion .accordion .accordion__title{
    background: none;
    padding: 5px 0;
    color: #287bbb;
    
  }
</style>



