<template>
  <section class="editor">
    <editor-toolbar></editor-toolbar>
    <editor-sections></editor-sections>
  </section>
</template>

<script>
import { getNewsletter, getTemplate } from "../vuex/actions";
import EditorToolbar from "./EditorToolbar";
import EditorSections from "./sections/Sections";

export default {
  vuex: {
    actions: { getNewsletter, getTemplate }
  },
  components: { EditorToolbar, EditorSections },
  route: {
    data ({ to }) {
      this.getNewsletter(to.params.newsletter);
      this.getTemplate();
    }
  }
};
</script>

<style lang="scss">
  .editor{
    display: flex;
  }
</style>



