<template>
  <section class="newsletter">
    <newsletter-create></newsletter-create>
    <newsletter-list></newsletter-list>
  </section>
</template>

<script>
import { getNewsletters } from "../vuex/actions";
import NewsletterCreate from "./NewsletterCreate";
import NewsletterList from "./NewsletterList";

export default {
  vuex: {
    actions: { getNewsletters }
  },
  components: { NewsletterCreate, NewsletterList },
  route: {
    data ({ to }) {
      this.getNewsletters();
    }
  }
};
</script>

<style lang="scss">
  .newsletter{
    padding: 30px;
  }
</style>



