import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

const state = {
  template: {},
  newsletters: [],
  newsletter: {},
  version: -1
};

const mutations = {
  GET_TEMPLATE (state, template) {
    state.template = template;

    console.log(state.template);
  },

  NEWSLETTERS_RECIEVED (state, newsletters) {
    state.newsletters = newsletters;
  },

  DELETED_NEWSLETTER (state, name) {
    state.newsletters.$remove(name);
  },

  NEWSLETTER_RECIEVED (state, newsletter) {
    state.newsletter = newsletter;
  },

  NEWSLETTER_SAVED (state, version) {
    state.version = version;
  },

  NEWSLETTER_CREATED (state, id) {
    state.id = id;
  },

  NEWSLETTER_EXPORTED (state) {
    console.log("exported");
  },

  DELETE_BLOCK_ITEM (state, index) {
    state.newsletter.blocks.splice(index, 1);
  },

  MOVE_SECTION (state, index, shift) {
    if (index < shift) {
      for (let i = index + 1; i <= shift; i++) {
        let dummy = state.newsletter.blocks[i - 1];
        state.newsletter.blocks.$set(i - 1, state.newsletter.blocks[i]);
        state.newsletter.blocks.$set(i, dummy);
      }
    } else {
      for (let i = index - 1; i >= shift; i--) {
        let dummy = state.newsletter.blocks[i + 1];
        state.newsletter.blocks.$set(i + 1, state.newsletter.blocks[i]);
        state.newsletter.blocks.$set(i, dummy);
      }
    }
  },

  ADD_SECTION (state, section) {
    var d = new Date();
    section.id = d.getTime();
    state.newsletter.blocks.$set(state.newsletter.blocks.length, section);
  },

  ADD_ARTICLE (state, index) {
    var article = state.template.blocks[1].articles[0];
    var d = new Date();

    article.id = d.getTime();
    console.log(article.id);
    state.newsletter.blocks[index].articles.$set(state.newsletter.blocks[index].articles.length, article);
  }
};

export default new Vuex.Store({
  state,
  mutations
});



