<template>
  <div class="newsletter-create">
    <input v-el:newsletter type="text" class="input newsletter-create__input" placeholder="Newsletter Name" v-model="name" v-if="opened" transition>
    <button class="newsletter-create__button" v-on:click="create" v-bind:class="{'newsletter-create__button--opened' : opened}">New</button>
  </div>
</template>

<script>
import { createNewsletter } from "../vuex/actions";
import Vue from "vue";

export default {
  data () {
    return {
      opened: false,
      name: ""
    };
  },

  vuex: {
    actions: { createNewsletter }
  },

  methods: {
    create () {
      if (this.opened && this.name) {
        this.createNewsletter(this.name);
        this.opened = false;
      } else {
        this.name = "";
        this.opened = true;

        Vue.nextTick(function () {
          this.$els.newsletter.focus();
        }.bind(this));
      }
    }
  }
};
</script>

<style lang="scss">
  @import "../assets/scss/button.scss";

  .newsletter-create{
    position: relative;
    width: 100%;
    display: flex;

    &__button{
      @extend .button;
      &--opened{
        border-radius: 0 5px 5px 0;
        width: 30%;
      }    
    }

    &__input{
      width: 75%;
    }

    >*{
      transition: width .25s;

      &.v-enter, &.v-leave{
        width: 0;
      }
    }
  }
  
</style>



