<template>
  <li class="newsletter-item">
    <span class="newsletter-item__delete" v-on:click="toggleDeleteState" v-if="!deleteState"><img src="../assets/icons/trash.svg" width="15"></span>

    <div v-link="'editor/' + newsletter.id" class="newsletter-item__link" v-if="!deleteState" transition>
      <div class="newsletter-item__image" v-bind:style="{ backgroundImage: 'url(' + newsletter.image + ')' }"></div>
      <p class="newsletter-item__name">{{ newsletter.name }}</p>
      <time class="newsletter-item__date">{{ newsletter.date | number | moment "from" }}</time>
    </div>
    
    <delete-confirmation v-if="deleteState" v-on:cancel="toggleDeleteState" v-on:delete="deleteNewsletter(newsletter.id)"></delete-confirmation>
  </li>
</template>

<script>
import { deleteNewsletter } from "../vuex/actions";
import DeleteConfirmation from "../ui/DeleteConfirmation";

export default {
  props: ["newsletter"],
  data () {
    return {
      deleteState: false
    };
  },
  components: { DeleteConfirmation },
  vuex: {
    actions: { deleteNewsletter }
  },
  methods: {
    toggleDeleteState () {
      this.deleteState = !this.deleteState;
    }
  }
};
</script>

<style lang="scss">
  @import "../assets/scss/button.scss";
  @import "../assets/scss/mixins.scss";

  .newsletter-item{
    color: #666;
    padding: 0;
    margin: 0px -30px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    height: 66px;
    
    &:last-child{
      border-bottom: none;
    }

    &:hover {
      background: rgba(#287bbb, 0.1);
      .newsletter-item__delete{
        display: block;
      }
    }

    &__delete{
      position: absolute;
      right: 35px;
      top: 23px;
      display: none;
      opacity: 0.4;
      z-index: 100;
    }

    &__link{
      @include clear-fix;
      padding: 10px 30px 10px;
      position: relative;
      transition: all .25s;
      transform: translate3d(0, 0, 0);

      &.v-enter, &.v-leave{
        transform: translate3d(-400px, 0, 0);
      }
    }

    &__image{
      width: 60px;
      height: 46px;
      background-position: center;
      background-size: cover;
      float: left;
      margin-right: 10px;
    }

    &__name{
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
      margin: 5px 0 0;
    }

    &__date{
      font-size: 11px;
      text-transform: uppercase;
      font-weight: bold;
      opacity: 0.7;
    }
  }
</style>



