<template>
  <div class="sections">
    <accordion v-for="block in newsletter.blocks" :title="block.type" track-by="id" v-drag-and-drop drop="handleDrop" :id="$index" v-on:delete="handleDelete($index)">
      <section-intro v-if="block.type == 'header'" v-bind:content.sync="block"></section-intro>
      <section-articles v-if="block.type == 'articles'" v-bind:content.sync="block" v-on:add="handleAddArticle($index)"></section-articles>
      <section-destination v-if="block.type == 'destination'" v-bind:content.sync="block"></section-destination>
      <section-featured v-if="block.type == 'featured_titles'" v-bind:content.sync="block"></section-featured>
      <section-promotions v-if="block.type == 'promotions'" v-bind:content.sync="block"></section-promotions>
    </accordion>
    <sections-add></sections-add>
  </div>
</template>

<script>
import { moveSection, deleteBlockItem, addArticle } from "../../vuex/actions";
import Accordion from "../../ui/Accordion";

import SectionIntro from "./SectionIntro";
import SectionArticles from "./SectionArticles";
import SectionDestination from "./SectionDestination";
import SectionFeatured from "./SectionFeatured";
import SectionPromotions from "./SectionPromotions";
import SectionsAdd from "./SectionsAdd";

export default {
  vuex: {
    getters: {
      newsletter: state => state.newsletter
    },
    actions: { moveSection, deleteBlockItem, addArticle }
  },
  components: { Accordion, SectionIntro, SectionArticles, SectionDestination, SectionFeatured, SectionPromotions, SectionsAdd },
  methods: {
    handleDrop (itemOne, itemTwo) {
      this.moveSection(itemOne.id, itemTwo.id);
    },

    handleDelete (index) {
      this.deleteBlockItem(index);
    },

    handleAddArticle (index) {
      this.addArticle(index);
    }

  }

};
</script>

<style lang="scss">
  .sections{
    position: absolute;
    top: 56px;
    left: 0;
    width: 100%;
    bottom: 0;
    overflow: hidden;
    box-sizing: border-box;
    overflow-y: scroll;
  }
</style>



