<template>
  <nav class="editor-toolbar">
    <a v-link="'/'"><img src="../assets/icons/back.svg" width="16"></a>
    <span class="spacer">&nbsp;</span>
    <a v-on:click="saveNewsletter(newsletter)">Save</a>
    <a v-on:click="exportNewsletter(newsletter)"><img src="../assets/icons/export.svg" width="16"></a>
  </nav>
</template>

<script>
import { saveNewsletter, exportNewsletter } from "../vuex/actions";

export default {
  vuex: {
    getters: {
      newsletter: state => state.newsletter
    },
    actions: { saveNewsletter, exportNewsletter }
  }
};
</script>

<style lang="scss">
  .editor-toolbar{
    padding: 0px;
    border-bottom: 1px solid #c2c2c2;
    z-index: 1000;
    width: 100%;
    height: 56px;
    display: flex;

    a{
      float: left;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 56px;
      height: 56px;
      cursor: pointer;
      color: #287bbb;
      font-weight: bold;
      text-transform: uppercase;
      font-size: 13px;
      line-height: 1;

      &:hover{
        background: #c9e3f4;
      }
    }

    .spacer{
      flex-grow: 10;
    }
  }
</style>



