    <text-field label="Section title" v-bind:value.sync="content.title"></text-field>
    <div class="ad" v-for="item in content.items">
      <text-field label="Ad URL" v-bind:value.sync="item.url" :inline="true"></text-field>
      <picture v-bind:value.sync="item.image"></picture>
    </div>
  </section>
</template>

<script>
import TextField from "../../ui/TextField";
import Picture from "../../ui/Picture";

export default {
  props: ["content"],
  components: {
    TextField, Picture
  }
};
</script>


<style lang="scss">
  .ad .picture {
    margin-top: -10px;
  }
</style>


