    <text-field label="Newsletter subtitle" v-bind:value.sync="content.subtitle"></text-field>
    <picture v-bind:value.sync="content.image" label="Newsletter main picture"></picture>
    <person label="Hero" v-bind:value.sync="content.hero"></person>
