<template>
  <div class="cta">
    <label v-if="label" class="cta__label">{{label}}:</label>
    <div class="cta__fields">
      <text-field v-bind:value.sync="value.label"></text-field>
      <text-field v-bind:value.sync="value.url"></text-field>
    </div>
  </div>
</template>

<script>
import TextField from "./TextField";
import Picture from "./Picture";

export default {
  props: ["value", "label"],
  components: {
    TextField, Picture
  }
};
</script>


<style lang="scss">
  @import "../assets/scss/ui";

  .cta{    
    &__fields{
      >* {margin-bottom: 5px};
    }

    &__label{
      @extend .label;
    }
  }
</style>



